<template>
    <div v-bind:style="{height: headHeight + 'px'}">
        <b-navbar variant="faded" type="light" style="background: #00b09b; background: linear-gradient(to left, #00b09b, #96c93d); boder:none;" v-bind:style="{height: headHeight + 'px'}">
            <div class="text-center w-100 text-white font-weight-bold">
                {{headname}}
            </div>
            <router-link class="position-absolute text-center" style="width:10%;height:100%;left:0" to="/">
                <img src="@/assets/Prev.png" alt="Prev" width="25">
            </router-link>
        </b-navbar>
    </div>
</template>

<script>
    export default {
        name: 'Head',
        props: ['headname'],
        data () {
            return {
                headHeight: window.innerHeight * 0.05,
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: "BMHANNA";
        src: url('/fonts/BMHANNA.ttf');
        font-weight: normal;
        font-style: normal;
    }
    .cfont{
        font: "BMHANNA";
        font-weight: bold;
    }
</style>


